import { Injectable, NgZone } from '@angular/core';
import { BehaviorSubject } from 'rxjs';
import { ActivatedRoute } from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class WebSocketServiceService {

  private ws: WebSocket;

  public wsData: BehaviorSubject<any>;


  private url: string;

  constructor(private route: ActivatedRoute) {
    this.wsData = new BehaviorSubject(null);
    this.initWebSocket();
    this.route.queryParams.subscribe(params => {
        this.url = params['simurl'];
        this.ws.close();
    });
   }


   public send(message: string) {
    this.ws.send(message);
   }


   private initWebSocket() {
    if (this.url === undefined || this.url === ''){
      this.ws = new WebSocket('ws:\\localhost:4202');
    } else {
      this.ws = new WebSocket('ws:\\' +this.url + ':4202');
    }
    
    this.ws.onmessage = e => {
      console.log('new message.', e.data);
      this.wsData.next(JSON.parse(e.data));
    }

    this.ws.onerror = e => {
      console.error('An error in the web socket.', e);
      this.ws.close();
    }

    this.ws.onclose = e => {
      console.warn('web socket connection closed.', e);
      let self = this;
      setTimeout(function() {
        self.initWebSocket();
      }, 1000);
    }

    this.ws.onopen = e => {
      console.log('web socket opened.', e);
    }
   }
}
