import { PipeTransform, Pipe } from '@angular/core';


@Pipe({name: 'ioHtmlId'})
export class IOHtmlIdPipe implements PipeTransform {

    transform(value: any) {
        const ioTitle: string = value;
        let id = ioTitle.substring(0, ioTitle.indexOf(' ('));
        id = id.split(' ').join('_');
        return id;
    }

}
