import { Component, OnInit, Input } from '@angular/core';
import { WebSocketServiceService } from 'src/app/web-socket-service.service';

@Component({
  selector: 'app-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.css']
})
export class SliderComponent implements OnInit {

  @Input() title: string;
  @Input() minValue: number;
  @Input() maxValue: number;
  @Input() address: string;
  @Input() value: number;
  @Input() gainOffset: any;
  
  constructor(private wsService: WebSocketServiceService) { }

  ngOnInit() {
    
  }

  conversionValue() {
    return 0;
  }

  checkValue(e) {
    let map = {};
    map['type'] = 'modBusSimulatorData';
    map['address'] = this.address;
    map['value'] = + e.target.value;

    this.wsService.send(JSON.stringify(map));
  }

}
