import { Component, OnInit } from '@angular/core';
import { WebSocketServiceService } from '../web-socket-service.service';
import * as _ from 'lodash';

@Component({
  selector: 'app-analog-inputs',
  templateUrl: './analog-inputs.component.html',
  styleUrls: ['./analog-inputs.component.css']
})
export class AnalogInputsComponent implements OnInit {

  analogInputs: any[];

  constructor(private wsService: WebSocketServiceService) { }

  ngOnInit() {
    this.wsService.wsData.subscribe(data => {
      if (data == null || data['type'] !== 'deviceStructure') {
        return;
      }
      let tempData = data['data']['AnalogInput'];
      if (!_.isEqual(tempData, this.analogInputs)) {
        this.analogInputs = tempData;
      }
    });
  }

}
