import { Component, OnInit } from '@angular/core';
import { WebSocketServiceService } from '../web-socket-service.service';

@Component({
  selector: 'app-digital-outputs',
  templateUrl: './digital-outputs.component.html',
  styleUrls: ['./digital-outputs.component.css']
})
export class DigitalOutputsComponent implements OnInit {

  digitalOutputs: any[];

  constructor(private wsService: WebSocketServiceService) { }

  ngOnInit() {
    this.wsService.wsData.subscribe(data => {
      if (data == null || data['type'] !== 'deviceStructure') {
        return;
      }
      this.digitalOutputs = data['data']['DigitalOutput'];
    });
  }

}
